import React from 'react';
import Tasks from './Tasks';
import Api from '../api';
import FilterTask from './FilterTask';
import {Modal} from '../ModalWindow';
import './Client.Tasks.css';
import FormTask from './FormTask';
import GroupTask from './GroupTask';

function formatTwoNumber(number) {
  return (number > 9) ? number : '0'+number;
}

function formatDate(date) {
  var y = date.getFullYear();
  var m = date.getMonth()+1;
  var d = date.getDate();

  return `${y}-${formatTwoNumber(m)}-${formatTwoNumber(d)}`;
}

class ClientTasks extends React.Component {
  constructor(props) {
    super(props);
    var date = new Date();
    var firstDay = new Date(date.getFullYear(), date.getMonth(), 1);
    var lastDay = new Date(date.getFullYear(), date.getMonth() + 1, 0);
    this.state = {
      error: false,
      loading: false,
      groups: [
      ],
      tasks: [{
        id: 0,
        task_date:'2019-08-19',
        task_client:'motoshar.ru',
        task_description:'description'
      }],
      filter: {
        task_parent: null,
        task_date:{
          from: formatDate(firstDay),
          to: formatDate(lastDay)
        }
      },
      modal: false,
      modalNew: false,
      formTask: {}
    }

    this.api = new Api({
      hostName: 'https://timetraker.herokuapp.com',
      //hostName: 'http://localhost:4005',
    });

    this.getTasks = this.getTasks.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
    this.onChange = this.onChange.bind(this);
    this.onShowTask = this.onShowTask.bind(this);
    this.onFilter = this.onFilter.bind(this);
  }

  onUpdate() {
    return this.api.update('tasks', this.state.formTask, {id: this.state.formTask.id})
    .then(() => this.setState(state => ({
      modal: false,
      madalNew: false,
      tasks: state.tasks
        .map(task => (task.id === this.state.formTask.id)? this.state.formTask: task)
    })))
    .catch(console.log);
  }

  onDel() {
    return this.api.del('tasks', this.state.formTask.id)
    .then(() => this.setState(state => ({
      modal: false,
      madalNew: false,
      tasks: state.tasks.filter(task => task.id !== this.state.formTask.id)
    })))
    .catch(console.log);
  }

  onShowTask(target, id) {
    if(target.classList.contains('Timer-btn')) {
      return undefined;
    }

    const task = this.state.tasks
      .find(task => task.id === id);

    this.setState(() => ({formTask: task, modal: true, modalNew: false}));
  }

  onChange({target}) {
    const keys = target.name.split('-');

    if(keys.length === 1) {
      return this.setState(state => ({
        [keys[0]]: target.value
      }))
    }

    if(keys.length === 2) {
      return this.setState(() => ({
        [keys[0]]: {
          ...this.state[keys[0]],
          [keys[1]]: target.value 
        }
      }));
    }

    this.setState(state => {
      var newState = {
        ...this.state, 
        [keys[0]]: {
          ...this.state[keys[0]],
          [keys[1]]: {
            ...this.state[keys[0]][keys[1]],
            [keys[2]]: target.value
          }
        }
      }

      this.getTasks(newState)

      return newState;
    });
  }

  onFilter({target}) {
    const keys = target.name.split('-');
    var value = target.value;

    if(target.value === 'null') {
       value = null;
    }

    var newState = {
      [keys[0]]: {
        ...this.state[keys[0]],
        [keys[1]]: value 
      }
    }
    
    return this.getTasks(newState);
  }

  getTasks(state) {
    if(!state) {
      state = this.state;
    }

    return this.api.list('tasks', state.filter)
    .then(response => {
      return this.setState(() => ({
        ...state,
        tasks: response,
        loading: false,
        error: false
      }));
    })
    .catch(err => 
      this.setState(() => ({...state, error: true, loading: false}))
    )
  }

  onSubmit(set) {
    for(var key in set) {
      if(set[key] === '') {
        return null;
      }
    }
    this.api.add('tasks', set)
    .then(() => this.getTasks({...this.state, modal: false}))
    .catch(console.log);
  }

  componentDidMount() {
    this.getTasks();
  }

  render() {
    console.log(this.state);
    return (
      <div>
        <FilterTask onChange={this.onChange}
          to={this.state.filter.task_date.to}
          from={this.state.filter.task_date.from}
        >
          <GroupTask value={this.state.filter.task_parent} onChange={this.onFilter}/>
          <div className="input-group-append">
            <button className="btn btn-outline-success"
              type="button"
              onClick={() => this.setState({
                modal: true,
                modalNew: true,
                formTask: {task_parent: this.state.filter.task_parent}
              })}
            >New Task</button>
          </div>
          <div className="input-group-append">
            <button className="btn btn-outline-success"
              type="button"
              onClick={() => {
                this.api.exportExcel('tasks', this.state.filter)
                .then(console.log)
                .catch(console.log)
              }}
            >Export excel</button>
          </div>
        </FilterTask>
        <Tasks
          tasks={this.state.tasks} 
          error={this.state.error}
          loading={this.state.loading}
          onShowTask={this.onShowTask}
        />
        <Modal show={this.state.modal} onClick={() => this.setState({
          modal: false,
        })}>
        {(this.state.modalNew)?[
          <h5 key="title" className="modal-title">New task</h5>,
          <FormTask key="fomr" {...this.state.formTask} onChange={this.onChange}/>,
          <button key="add" onClick={() => this.onSubmit(this.state.formTask)} className="btn btn-success">Add</button>
        ]: [
          <h5 key="title" className="modal-title">Show task</h5>,
          <FormTask key="form" {...this.state.formTask} onChange={this.onChange}/>,
          <button key="delete" onClick={() => this.onDel()} className="btn btn-danger">{'delete'}</button>,
          <button key="update" onClick={() => this.onUpdate()} className="btn btn-success">Update</button>
        ]}
        </Modal>
      </div>
    );
  }
}

export default ClientTasks;
